@import "../styles/utils/mixins.scss";

.rating {
  @extend %roboto-400;
  display: flex;
  justify-content: space-between;
  gap: 19px;
  white-space: nowrap;
  width: 168px;
  height: 16px;
}

.card__rating {
  margin-right: $side-margins-text;
  margin-left: $side-margins-text;
  margin-bottom: 16px;
}

.rating__stars {
  display: flex;
  gap: 4px;
}

.rating__star {
  @include square($star-size);
  background: url("/images/star-active.svg") center no-repeat;
}

.rating__stars :last-child {
  @include square($star-size);
  background: url("/images/star.svg") center no-repeat;
}